import React, { Component } from "react";
import { connect } from "react-redux";
import * as actions from "../../store/actions/index";
import { NavLink } from "react-router-dom";

import "./AuthForm.css";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import AuthInput from "../../components/UI/AuthComponents/AuthInput";
import AuthInputPassword from "../../components/UI/AuthComponents/AuthInputPassword";
import AuthButton from "../../components/UI/AuthComponents/AuthButton";
import CSSAlert from "../../components/UI/AuthComponents/AuthAlert";
import Typography from "@material-ui/core/Typography";
import Link from "@material-ui/core/Link";

// Ð ÐµÐ°Ð»Ð¸Ð·Ð°Ñ†Ð¸Ñ ÐºÐ»Ð°ÑÑÐ¾Ð²Ð¾Ð³Ð¾ ÐºÐ¾Ð¼Ð¿Ð¾Ð½ÐµÐ½Ñ‚Ð° Ñ„Ð¾Ñ€Ð¼Ñ‹
class AuthForm extends Component {
  state = {
    email: "",
    password: "",
    rememberMe: false,
    validateFields: {
      email: false,
      password: false,
    },
    fieldValidationErrors: {
      email: "ÐžÐ±ÑÐ·Ð°Ñ‚ÐµÐ»ÑŒÐ½Ð¾Ðµ Ð¿Ð¾Ð»Ðµ!",
      password: "ÐžÐ±ÑÐ·Ð°Ñ‚ÐµÐ»ÑŒÐ½Ð¾Ðµ Ð¿Ð¾Ð»Ðµ!",
    },
    willSend: true,
  };

  //Ð¤ÑƒÐ½ÐºÑ†Ð¸Ñ Ð´Ð»Ñ Ð¾Ð±Ð½Ð¾Ð²Ð»ÐµÐ½Ð¸Ñ ÑÐ¾ÑÑ‚Ð¾ÑÐ½Ð¸Ñ Ð¿Ð¾Ð»ÐµÐ¹ Ð²Ð²Ð¾Ð´Ð°
  inputHandler = (event) => {
    const name = event.target.name;
    const value = event.target.value;
    this.validation(name, value);

    //ÐžÐ±Ð½Ð¾Ð²Ð»ÐµÐ½Ð¸Ðµ State Ð·Ð½Ð°Ñ‡ÐµÐ½Ð¸Ñ Ð¿Ð¾Ñ‡Ñ‚Ñ‹ Ð¸Ð»Ð¸ Ð¿Ð°Ñ€Ð¾Ð»Ñ
    this.setState({ [name]: value });
  };

  //Ð’Ð°Ð»Ð¸Ð´Ð°Ñ†Ð¸Ñ
  validation = (name, value) => {
    const reg = /^([\w.%+-]+)@([\w-]+\.)+([\w]{2,})$/i;
    let validateFieldsUpdate = { ...this.state.validateFields };
    let fieldValidationErrors = { ...this.state.fieldValidationErrors };
    switch (name) {
      case "email":
        validateFieldsUpdate.email = reg.test(value);
        if (value.length === 0) {
          fieldValidationErrors.email = "ÐžÐ±ÑÐ·Ð°Ñ‚ÐµÐ»ÑŒÐ½Ð¾Ðµ Ð¿Ð¾Ð»Ðµ!";
        } else fieldValidationErrors.email = "ÐŸÑ€Ð¾Ð²ÐµÑ€ÑŒÑ‚Ðµ ÐºÐ¾Ñ€Ñ€ÐµÐºÑ‚Ð½Ð¾ÑÑ‚ÑŒ Ð¿Ð¾Ñ‡Ñ‚Ñ‹";
        break;
      case "password":
        validateFieldsUpdate.password = value.length >= 6;
        if (value.length === 0) {
          fieldValidationErrors.password = "ÐžÐ±ÑÐ·Ð°Ñ‚ÐµÐ»ÑŒÐ½Ð¾Ðµ Ð¿Ð¾Ð»Ðµ!";
        } else
          fieldValidationErrors.password =
            "ÐŸÐ°Ñ€Ð¾Ð»ÑŒ Ð´Ð¾Ð»Ð¶ÐµÐ½ Ð±Ñ‹Ñ‚ÑŒ Ð±Ð¾Ð»ÐµÐµ 6 ÑÐ¸Ð¼Ð²Ð¾Ð»Ð¾Ð²";
        break;
      default:
        break;
    }
    this.setState({
      validateFields: validateFieldsUpdate,
      fieldValidationErrors: fieldValidationErrors,
    });
  };

  //Ð¤ÑƒÐ½ÐºÑ†Ð¸Ñ Ð´Ð»Ñ Ð¾Ð±Ð½Ð¾Ð²Ð»ÐµÐ½Ð¸Ñ ÑÐ¾ÑÑ‚Ð¾ÑÐ½Ð¸Ñ Ñ‡ÐµÐºÐ±Ð¾ÐºÑÐ°
  checkboxHandler = () => {
    let checkbox = this.state.rememberMe;

    this.setState({ rememberMe: !checkbox });
  };

  //Ð¤ÑƒÐ½ÐºÑ†Ð¸Ñ Ð´Ð»Ñ Ð¾Ñ‚Ð¿Ñ€Ð°Ð²ÐºÐ¸ Ð´Ð°Ð½Ð½Ñ‹Ñ… Ñ„Ð¾Ñ€Ð¼Ñ‹
  formSenderHandler = () => {
    //ÐŸÑ€Ð¾Ð²ÐµÑ€ÐºÐ° Ð½Ð° Ð·Ð°Ð¿Ð¾Ð»Ð½ÐµÐ½Ð½Ñ‹Ðµ Ð¿Ð¾Ð»Ñ
    if (this.state.validateFields.email && this.state.validateFields.password) {
      this.setState({ willSend: true });
      //ÐžÑ‚Ð¿Ñ€Ð°Ð²ÐºÐ° POST Ð·Ð°Ð¿Ñ€Ð¾ÑÐ° Ð½Ð° backend
      this.props.onAuth(
        this.state.email,
        this.state.password
      );
    } else {
      this.setState({ willSend: false });
    }
  };

  render() {
    //Ð’ÑÐ¿Ð¾Ð¼Ð¾Ð³Ð°Ñ‚ÐµÐ»ÑŒÐ½Ñ‹Ðµ Ð¿ÐµÑ€ÐµÐ¼ÐµÐ½Ð½Ñ‹Ðµ Ð´Ð»Ñ Ð¾Ñ‚Ð¾Ð±Ñ€Ð°Ð¶ÐµÐ½Ð¸Ñ Ð²Ð°Ð»Ð¸Ð´Ð°Ñ†Ð¸Ð¸ Ð¸ Ð¿Ð¾Ð´ÑÐºÐ°Ð·ÐºÐ¸
    let emailError = false;
    let passwordError = false;

    if (!this.state.willSend) {
      emailError = !this.state.validateFields.email;
      passwordError = !this.state.validateFields.password;
    }

    return (
      <div className="auth_container">
        <form>
          <div className="lockIconContainer">
            <div className="roundIcon">
              <LockOutlinedIcon />
            </div>
          </div>
          <Typography className="authHeader" variant="h5" align="center">
            Ð¡Ð¸ÑÑ‚ÐµÐ¼Ð° ÐºÐ¾Ð¼Ð¼ÑƒÐ½Ð°Ð»ÑŒÐ½Ñ‹Ñ… Ð¿Ð¾ÐºÐ°Ð·Ð°Ñ‚ÐµÐ»ÐµÐ¹
          </Typography>
          {this.props.errorMessage ? (
            <CSSAlert severity="error">{this.props.errorMessage}</CSSAlert>
          ) : null}
          <AuthInput
            key="email"
            id="email"
            label="ÐŸÐ¾Ñ‡Ñ‚Ð°"
            name="email"
            error={emailError}
            helperText={
              emailError ? this.state.fieldValidationErrors.email : null
            }
            onChange={this.inputHandler}
            value={this.state.email}
          />
          <AuthInputPassword
            key="password"
            id="password"
            label="ÐŸÐ°Ñ€Ð¾Ð»ÑŒ"
            name="password"
            error={passwordError}
            helperText={
              passwordError ? this.state.fieldValidationErrors.password : null
            }
            onChange={this.inputHandler}
            value={this.state.password}
          />
          <AuthButton onClick={this.formSenderHandler}>
            Ð’Ð¾Ð¹Ñ‚Ð¸ Ð² Ð°ÐºÐºÐ°ÑƒÐ½Ñ‚
          </AuthButton>
          <Typography>
            <Link component={NavLink} to="/registration" variant="body2">
              Ð•Ñ‰Ðµ Ð½ÐµÑ‚ Ð°ÐºÐºÐ°ÑƒÐ½Ñ‚Ð°? Ð ÐµÐ³Ð¸ÑÑ‚Ñ€Ð°Ñ†Ð¸Ñ
            </Link>
          </Typography>
        </form>
      </div>
    );
  }
}

//Ð˜ÑÐ¿Ð¾Ð»ÑŒÐ·Ð¾Ð²Ð°Ð½Ð¸Ðµ Redux
const mapStateToProps = (state) => {
  return {
    isAuth: state.token !== null,
    loading: state.loading,
    errorMessage: state.error,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    onAuth: (email, password) => dispatch(actions.auth(email, password)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(AuthForm);
